import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
export default new Vuex.Store({
    state : {
        showsidebar: true,              /*  */
        showminilist: true,             /*  */
        isplay:false,                   /*  */
        playtime: 0,                    /*  */
        duration: 0,                    /*  */
        typenum: 0,
        type : ["icon-suijibofang","icon-liebiaoxunhuan","icon-danquxunhuan"],
        describing: ["","",""],

    },
    mutations:{
        change(state){
            state.showsidebar= !state.showsidebar
        },
        changemini(state){
            state.showminilist= !state.showminilist
        },
        changeplay(state){
            state.isplay= !state.isplay
        },
        changetime(state,b){
            state.playtime = b
        },
        getsumtime(state,b){
            state.duration = b
        },
        changetype(state){
            state.typenum++;
            state.typenum= state.typenum>2 ? 0 : state.typenum
        }


    }
})


