<template>
   <swiper  :options="swiperOption" ref="mySwiper" class="hid">
    <!-- slides -->
    <swiper-slide class="red" v-for="(v,k) in list"><a :href="list[k].linkUrl"><img  :src="list[k].picUrl"></a></swiper-slide>    
    <!-- Optional controls -->
    <div class="swiper-pagination"  slot="pagination"></div>
  </swiper>
</template>

<script>

require("swiper/dist/css/swiper.css");
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  name: "carrousel",
  props:["list"],
  data() {
    return {
      
      swiperOption: {
        //apihttp://www.swiper.com.cn/api/
        // notNextTicknotNextTicktrueNextTickswiperswiper<br>swipertrue
        notNextTick: true,
        // swiper configs swiperapi
        autoplay: 1500,
        loop: true,
        initialSlide: 5,
        direction: "horizontal",
        //grabCursor : true,
        setWrapperSize: true,
        autoplayDisableOnInteraction: false, //true
        pagination: ".swiper-pagination",
        paginationClickable: true,
        mousewheelControl: true,
        observeParents: true
      }
    };
  },
  components: {
    swiper,
    swiperSlide
  },
  
  
};
</script>

<style lang="scss" scoped>
$sc: 25;
.container{
    height: 160/$sc+rem;
    position: fixed;
    top: 4rem;
  
}

.swiper-slide {
    width: 100%;
  img {
    width: 100%;
    height: 160/$sc+rem;
  }
}
</style>


